#login-table{
    padding: 10px 80px;
}

#login-fieldset{
    width: fit-content;
    margin: auto;
    margin-top: 100px;
}

#login-form{
    width: 450px;
    height: 245px;
}

@media only screen and (max-width: 500px) {
    #login-form{
        width: 300px;
        height:calc( 100vh * 1/3.5 );
    }
    #div-auth{
        font-size: calc( 100vw * 1/45 );
    }
    #login{
        width: calc( 100vw * 1/5 );
        height: calc( 100vh * 1/70 );
    }
    #password{
        width: calc( 100vw * 1/5 );
        height: calc( 100vh * 1/70 );
    }
    #login-table{
        padding: 0 0;
        padding-left: calc( 100vw * 1/10 );
    }
    .change_button, .auth_button{
        font-size: calc( 100vh * 1/80 );
        padding: calc( 100vh * 1/170 ) 3px;
        margin-top: 1px;
    }
    .auth_button{
        margin-top: calc( 100vh * 1/80 );
    }
    #login-fieldset{
        margin-top: calc( 100vw * 1/4 );
    }

}

fieldset legend{
    padding-top: 30px;
}

.login_error {
    color:red;
    height: 20px;
}